<template>
  <div
    class="modal fade"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="pdfModal"
  >
    <div class="modal-dialog modal-xl">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5">Modal title</h1>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <section>
            <div ref="pdf">
              <h1 class="text-center mb-5">工程承攬合約書</h1>
              <div class="h4 d-flex ms-5 mb-4">
                <div>立合約書人：</div>
                <div class="d-flex flex-column">
                  <div class="mb-3">業主＿＿＿公司(以下稱甲方)</div>
                  <div>承包商＿＿＿公司(以下稱乙方)</div>
                </div>
              </div>
              <div class="h4">
                茲因乙方承包甲方______工程，雙方同意訂定合約條款如下，以資互相遵守:
              </div>
              <div>
                一、 工程位置:本件工程座落於 縣市 鄉鎮市區 路街 段 巷 弄號樓
              </div>
              <div>
                二、 工程圖說明:乙方應按經甲方認可之設計圖說規格切實施工。(圖說如附件)
              </div>
            </div>
          </section>
        </div>
        <div class="modal-footer">
          <button class="btn btn-dark" @click="download">Download PDF</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from 'bootstrap';
// eslint-disable-next-line import/no-extraneous-dependencies
import { jsPDF } from 'jspdf';
// eslint-disable-next-line import/no-extraneous-dependencies
import * as html2canvas from 'html2canvas';

export default {
  data() {
    return {
      pdfModal: '',
      tempData: {},
    };
  },
  methods: {
    download() {
      const windowHeight = window.innerHeight;
      const windowWidth = window.innerWidth;
      // eslint-disable-next-line new-cap
      const pdf = new jsPDF();

      const canvasElement = document.createElement('canvas');
      canvasElement.width = windowWidth;
      canvasElement.height = windowHeight;

      html2canvas(this.$refs.pdf, {
        canvas: canvasElement,
        width: windowWidth,
        height: windowHeight,
      })
        .then((canvas) => {
          const img = canvas.toDataURL('image/jpeg', 1);
          //   document.body.appendChild(canvas);
          pdf.addImage(img, 'JPEG', 10, 10);
          pdf.save('sample.pdf');
        })
        .catch((err) => {
          console.log(err);
        });
    },
    showModal() {
      this.pdfModal.show();
    },
  },
  mounted() {
    this.pdfModal = new Modal(this.$refs.pdfModal);
    this.$emitter.on('pdfEmit', (e) => {
      this.tempData = e;
      this.showModal();
    });
  },
};
</script>
